import React, { useEffect } from "react";

const HomeSlideItem = ({ src, alt, text }) => (
  <div className="homeslide-item">
    <img src={process.env.PUBLIC_URL + `/img/${src}`} alt={alt} />
    <div className="homeslide-item-text">
      <h5>{text}</h5>
    </div>
  </div>
);

const Home = () => {
  useEffect(() => {
    const Scroll = document.querySelector("#scroll");
    const outterText = document.querySelector(".outterText");
    const innerText = document.querySelector(".innerText");
    const home = document.querySelector(".homeslide");
    const arrowUp = document.querySelector(".arrow-up");

    const onScroll = () => {
      const value = window.scrollY;
      Scroll.style.clipPath = `circle(${value - 200}px at center center)`;
      outterText.style.left = `${100 - value / 8}%`;
      innerText.style.left = `${100 - value / 8}%`;
      home.style.opacity =
        1 - window.scrollY / home.getBoundingClientRect().height;

      if (window.scrollY > home.getBoundingClientRect().height / 2) {
        arrowUp.classList.add("visible");
      } else {
        arrowUp.classList.remove("visible");
      }
    };

    const onArrowUpClick = () => {
      window.scrollTo({ top: 0, behavior: "smooth" });
    };

    window.addEventListener("scroll", onScroll);
    arrowUp?.addEventListener("click", onArrowUpClick);

    return () => {
      window.removeEventListener("scroll", onScroll);
      arrowUp?.removeEventListener("click", onArrowUpClick);
    };
  }, []);

  const images = [
    { src: "bg-esimTablet.png", alt: "", text: "TDS eSIM." },
    { src: "bg-TW-myshop.png", alt: "", text: "T world." },
    { src: "bg-youtube.png", alt: "", text: "youtube." },
    { src: "bg-universe.png", alt: "", text: "universe." },
    { src: "bg-kakao.png", alt: "", text: "kakao." },
    { src: "bg-seashore.png", alt: "", text: "eashore." },
  ];

  return (
    <section id="home">
      <div className="homeslide">
        {images.map((image, index) => (
          <HomeSlideItem key={index} {...image} />
        ))}
      </div>

      <div className="scroll-wrap">
        <h2 className="outterText">안녕하세요 설소윤의 포트폴리오입니다!</h2>
        <div id="scroll">
          <h2 className="innerText">안녕하세요 설소윤의 포트폴리오입니다!</h2>
        </div>
      </div>
    </section>
  );
};

export { Home as default };
