import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination } from "swiper/modules";
import gsap from "gsap";
import "swiper/css/navigation";
import "swiper/css/pagination";
import SlideContent from "../components/SliderContent";
import slideApi from "../components/SliderApi";

function Project() {
  const handleSlideChange = () => {
    gsap.to(".slide-text span", {
      duration: 0.2,
      y: "-100px",
    });
    gsap.to(".slide-number span", {
      duration: 0.2,
      x: "-130px",
    });
    gsap.to(".swiper-slide-prev", {
      duration: 0.5,
      scale: 0.85,
      ease: "power4.out",
    });
  };

  const handleSlideChangeTransitionEnd = () => {
    gsap.to(".slide-text span", {
      duration: 0.2,
      y: 0,
      delay: 0.5,
    });
    gsap.to(".slide-number span", {
      duration: 0.2,
      x: 0,
      delay: 0.7,
    });
    gsap.to(".swiper-slide-active", {
      duration: 0.5,
      scale: 1,
      ease: "power4.out",
    });
  };
  return (
    <section id="project" className="section">
      <div className="bg-text">
        <h1>projects.</h1>
      </div>
      <h5 className="main-name top">projects.</h5>

      <Swiper
        modules={[Navigation, Pagination]}
        navigation={true}
        pagination={{ clickable: true }}
        slidesPerView={1}
        onSlideChangeTransitionStart={handleSlideChange}
        onSlideChangeTransitionEnd={handleSlideChangeTransitionEnd}
        speed={500}
        observer={true}
        observeParents={true}
      >
        {slideApi.map((slideApi, index) => (
          <SwiperSlide key={index}>
            <SlideContent {...slideApi} />
          </SwiperSlide>
        ))}
      </Swiper>
    </section>
  );
}

export { Project as default };
