import React, { useEffect } from "react";
import "./scss/style.scss";
import Loader from "./js/screen/Loader.js";
import Home from "./js/screen/Home.js";
import Project from "./js/screen/Project.js";
import About from "./js/screen/About.js";
import Contact from "./js/screen/Contact.js";
import { faAngleDoubleUp, faBars } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

function App() {
  useEffect(() => {
    const sectionIds = ["#home", "#about", "#project", "#contact"];
    const sections = sectionIds.map((id) => document.querySelector(id));
    const navItems = sectionIds.map((id) =>
      document.querySelector(`[data-link="${id}"]`)
    );

    let selectedNavIndex = 0;
    let selectedNavItem = navItems[0];

    function selectNavItem(selected) {
      selectedNavItem.classList.remove("active");
      selectedNavItem = selected;
      selectedNavItem.classList.add("active");
    }

    function scrollIntoView(selector) {
      const scrollTo = document.querySelector(selector);
      scrollTo.scrollIntoView({ behavior: "smooth" });
      selectNavItem(navItems[sectionIds.indexOf(selector)]);
    }

    const observerOptions = {
      root: null,
      rootMargin: "0px",
      threshold: 0.3,
    };

    const observerCallback = (entries, observer) => {
      entries.forEach((entry) => {
        if (!entry.isIntersecting && entry.intersectionRatio > 0) {
          const index = sectionIds.indexOf(`#${entry.target.id}`);
          if (entry.boundingClientRect.y < 0) {
            selectedNavIndex = index + 1;
          } else {
            selectedNavIndex = index - 1;
          }
          selectNavItem(navItems[selectedNavIndex]);
        }
      });
    };

    const observer = new IntersectionObserver(
      observerCallback,
      observerOptions
    );
    sections.forEach((section) => observer.observe(section));

    window.addEventListener("wheel", () => {
      if (window.scrollY === 0) {
        selectedNavIndex = 0;
        // console.log(0);
      } else if (
        Math.round(window.scrollY + window.innerHeight) >= document.scrollHeight
      ) {
        selectedNavIndex = navItems.length - 1;
        // console.log(2);
      }
      selectNavItem(navItems[selectedNavIndex]);
    });

    const navbar = document.querySelector("#navbar");
    const navbarHeight = navbar.getBoundingClientRect().height;
    const navbarMenu = document.querySelector(".navbar__menu");

    document.addEventListener("scroll", () => {
      if (window.scrollY > navbarHeight) {
        navbar.classList.add("navbar--dark");
      } else {
        navbar.classList.remove("navbar--dark");
      }
    });

    // Handle scrolling when tapping on the navbar menu
    navbarMenu.addEventListener("click", (event) => {
      const target = event.target;
      const link = target.dataset.link;
      if (link == null) {
        return;
      }
      navbarMenu.classList.remove("open");
      scrollIntoView(link);
    });

    // Navbar toggle button for small screen
    const navbarToggleBtn = document.querySelector(".navbar__toggle-btn");
    navbarToggleBtn.addEventListener("click", () => {
      navbarMenu.classList.toggle("open");
    });

    return () => {
      document.removeEventListener("scroll", () => {});
      navbarMenu.removeEventListener("click", (event) => {});
      navbarToggleBtn.removeEventListener("click", () => {});
    };
  }, []);

  return (
    <div className="App">
      <Loader />
      <nav id="navbar">
        <div className="navbar__logo">
          <a href="index.html">S.</a>
        </div>
        <ul className="navbar__menu">
          <li className="navbar__menu__item active" data-link="#home">
            home.
          </li>
          <li className="navbar__menu__item" data-link="#about">
            about.
          </li>
          <li className="navbar__menu__item" data-link="#project">
            project.
          </li>
          <li className="navbar__menu__item" data-link="#contact">
            contact.
          </li>
        </ul>
        <button className="navbar__toggle-btn">
          <FontAwesomeIcon icon={faBars} />
        </button>
      </nav>
      <Home />
      <About />
      <Project />
      <Contact />
      <button className="arrow-up">
        <FontAwesomeIcon icon={faAngleDoubleUp} className="arrow-up-ico" />
      </button>
    </div>
  );
}

export default App;
