import React, { useEffect } from "react";

const Loader = () => {
  useEffect(() => {
    const loaderBox = document.getElementById("loader_box");

    const handleLoad = () => {
      setTimeout(() => {
        loaderBox.style.display = "none";
      }, 500);
    };

    setTimeout(handleLoad, 9500);

    return () => {
      // Clean up the event listener when the component unmounts
      document.removeEventListener("DOMContentLoaded", handleLoad);
    };
  }, []);

  return (
    <div id="loader_box">
      <div className="background">
        <div className="background-wave"></div>
        <div className="background-wave"></div>
      </div>
      <div className="column">
        <span className="column-bottom">2024</span>
        <div className="column-line">
          <div className="line"></div>
        </div>
        <span className="column-top">Portfolio</span>
      </div>

      <div className="slideshow">
        <div className="slideshow-item">
          <div className="slideshow-item__text">
            <span className="filled">s</span>
            <span className="filled">s</span>
            <span className="filled">y</span>
          </div>
        </div>
        <div className="slideshow-item">
          <div className="slideshow-item__text slide-name">
            <div className="first-name">
              <span className="filled">s</span>
              <span className="filled-not">eol</span>
            </div>
            <span className="filled-not">so</span>
            <span className="filled-not">yoon</span>
          </div>
        </div>
        <div className="slideshow-item">
          <div className="slideshow-item__text">
            <span className="filled">s</span>
            <span className="filled-not">tart</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Loader;
